import React, { memo } from "react";
import { NodeProps } from "reactflow";

interface EventNodeData {
  label: string;
  date: string;
}

const EventNode = ({ data }: NodeProps<EventNodeData>) => {
  return (
    <div
      style={{
        background: "#fff",
        border: "1px solid #ddd",
        padding: "10px",
        borderRadius: "5px",
        maxWidth: "120px",
        textAlign: "left",
        pointerEvents: "none", // This makes the node non-interactive
      }}
    >
      <div style={{ fontSize: 12, fontWeight: "bold" }}>{data.label}</div>
      <div style={{ fontSize: 10, color: "#888" }}>
        {new Date(data.date).toLocaleDateString()}
      </div>
    </div>
  );
};

export default memo(EventNode);
