import React, { useEffect, useState, useCallback } from "react";
import ReactFlow, {
  Node,
  Edge,
  Controls,
  Background,
  useNodesState,
  useEdgesState,
  addEdge,
  Connection,
  NodeTypes,
  NodeChange,
  Panel,
  PanOnScrollMode,
} from "reactflow";
import "reactflow/dist/style.css";
import { collection, query, where, getDocs } from "firebase/firestore";
import { db } from "./firebaseConfig";
import EventNode from "./EventNode";
import YearNode from "./YearNode";

interface Event {
  id: string;
  name: string;
  startDate: string;
  endDate: string;
  tag: string;
}

interface TimelineFlowProps {
  userId: string;
}

const nodeTypes: NodeTypes = {
  eventNode: EventNode,
  yearNode: YearNode,
};

const TimelineFlow: React.FC<TimelineFlowProps> = ({ userId }) => {
  const [events, setEvents] = useState<Event[]>([]);
  const [nodes, setNodes, onNodesChange] = useNodesState([]);
  const [edges, setEdges, onEdgesChange] = useEdgesState([]);

  useEffect(() => {
    const fetchEvents = async () => {
      if (!userId) {
        console.log("No userId provided, skipping event fetch");
        return;
      }

      console.log("Fetching events for userId:", userId);
      const q = query(collection(db, "events"), where("userId", "==", userId));
      const querySnapshot = await getDocs(q);
      const fetchedEvents = querySnapshot.docs.map(
        (doc) =>
          ({
            id: doc.id,
            ...doc.data(),
          } as Event)
      );
      console.log("Fetched events:", fetchedEvents);
      setEvents(fetchedEvents);
    };

    fetchEvents();
  }, [userId]);

  useEffect(() => {
    if (events.length === 0) {
      setNodes([]);
      setEdges([]);
      return;
    }

    const sortedEvents = [...events].sort(
      (a, b) =>
        new Date(a.startDate).getTime() - new Date(b.startDate).getTime()
    );
    const startDate = new Date(sortedEvents[0].startDate);
    const endDate = new Date(sortedEvents[sortedEvents.length - 1].startDate);

    const startYear = startDate.getFullYear();
    const endYear = endDate.getFullYear() + 1; // Include one year after the last event
    const years = Array.from(
      { length: endYear - startYear + 1 },
      (_, i) => startYear + i
    );

    const yearWidth = 400; // Increased width for each year
    const timelineWidth = years.length * yearWidth;

    // Create a map to track the row for each event type
    const eventTypeRows = new Map<string, number>();
    let currentRow = 0;

    const timelineNodes: Node[] = sortedEvents.map((event) => {
      const eventDate = new Date(event.startDate);
      const yearIndex = eventDate.getFullYear() - startYear;
      const daysIntoYear =
        (eventDate.getTime() -
          new Date(eventDate.getFullYear(), 0, 1).getTime()) /
        (1000 * 3600 * 24);
      const xPosition =
        yearIndex * yearWidth + (daysIntoYear / 365) * yearWidth;

      // Assign a row to the event type if it doesn't have one
      if (!eventTypeRows.has(event.tag)) {
        eventTypeRows.set(event.tag, currentRow++);
      }
      const yPosition = 100 + eventTypeRows.get(event.tag)! * 50; // 50px vertical spacing between rows

      return {
        id: event.id,
        type: "eventNode",
        data: {
          label: event.name,
          date: event.startDate,
        },
        position: { x: xPosition, y: yPosition },
        draggable: false,
      };
    });

    const timelineYearNodes: Node[] = years.map((year, index) => {
      return {
        id: `year-${year}`,
        type: "yearNode",
        data: { label: year.toString() },
        position: { x: index * yearWidth, y: currentRow * 50 + 150 },
        draggable: false,
      };
    });

    setNodes([...timelineNodes, ...timelineYearNodes]);
    setEdges([]);
  }, [events, setNodes, setEdges]);

  const onConnect = useCallback(
    (params: Connection) => setEdges((eds) => addEdge(params, eds)),
    [setEdges]
  );

  const handleNodesChange = useCallback(
    (changes: NodeChange[]) => {
      // Filter out all changes to make nodes completely non-interactive
      const filteredChanges = changes.filter(
        (change) => change.type !== "position" && change.type !== "select"
      );
      onNodesChange(filteredChanges);
    },
    [onNodesChange]
  );

  return (
    <div
      style={{
        width: "calc(100vw - 32px)",
        height: "calc(100vh - 32px)",
        position: "absolute",
        top: "16px",
        left: "16px",
      }}
    >
      {events.length > 0 ? (
        <ReactFlow
          nodes={nodes}
          edges={edges}
          onNodesChange={handleNodesChange}
          onEdgesChange={onEdgesChange}
          onConnect={onConnect}
          nodeTypes={nodeTypes}
          fitView
          nodesDraggable={false}
          nodesConnectable={false}
          elementsSelectable={false}
          panOnScroll={true}
          panOnScrollMode={PanOnScrollMode.Free}
          panOnDrag={true}
          zoomOnScroll={false}
          zoomOnPinch={true}
          zoomOnDoubleClick={false}
        >
          <Background color="#aaa" gap={16} />
          <Controls position="top-right" />
        </ReactFlow>
      ) : (
        <div
          style={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            height: "100%",
          }}
        >
          Loading...
        </div>
      )}
    </div>
  );
};

export default TimelineFlow;
