import React, { memo } from "react";
import { NodeProps } from "reactflow";

interface YearNodeData {
  label: string;
}

const YearNode = ({ data }: NodeProps<YearNodeData>) => {
  return (
    <div
      style={{
        fontSize: "10",
        padding: "5px",
        textAlign: "left",
        pointerEvents: "none",
        color: "#888",
      }}
    >
      {data.label}
    </div>
  );
};

export default memo(YearNode);
